.form {
  border: 1px solid;
  padding: 20px;
  width: 500px;
}

.btn {
  cursor: pointer; 
  border-radius: 5px; 
  font-size: 20px; 
  
}
.btn:hover {
  background-color: aquamarine;
} 

label {
  display: block;
  margin-bottom: 30px;
  font-size: 30px;
  line-height: 1.3;
}

input {
  display: block;
  width: 300px;
  height: 30px;
  font-size: 20px;
}